@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300&display=swap);
.welcome {
   font-size: min(6.5vh, 14vw);
   font-weight: 500;
   max-width: 700px;
}


.web-link  > a {
   color: rgb(39, 164, 180);
   font-size: 1.2rem;
   margin-right: 10px;
   text-decoration: none;
   font-weight: 300;
}

.welcome, .web-link > a {
   padding: 15px;
   font-weight: 400;
}
body {
  background: rgb(187, 193, 194);
  color: white;
  height: 100%;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  width: 100%;
  overflow: hidden;
}


.App  {
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
}

